<template>
  <v-app id="app">
    <ContactBar></ContactBar>
    <NavBar></NavBar>
    <v-main fluid class="main-wrapper grey lighten-5">
      <router-view />
    </v-main>

    <new-content-available-toastr
      v-if="newContentAvailable"
      class="new-content-available-toastr"
      :refreshing-app="refreshingApp"
      @refresh="serviceWorkerSkipWaiting"
    ></new-content-available-toastr>
    <apple-add-to-home-screen-modal
      v-if="showAddToHomeScreenModalForApple"
      class="apple-add-to-home-screen-modal"
      @close="closeAddToHomeScreenModalForApple(false)"
    >
    </apple-add-to-home-screen-modal>
  </v-app>
</template>
<script>
import NavBar from '@/components/NavBar'
import ContactBar from '@/components/ContactBar'
import NewContentAvailableToastr from '@/components/NewContentAvailableToastr'
import AppleAddToHomeScreenModal from '@/components/AppleAddToHomeScreenModal'
import { mapState, mapActions, mapGetters } from 'vuex'

export default {
  components: {
    NavBar,
    ContactBar,
    NewContentAvailableToastr,
    AppleAddToHomeScreenModal
  },
  computed: {
    ...mapGetters('app', ['newContentAvailable']),
    ...mapState('app', ['showAddToHomeScreenModalForApple', 'refreshingApp'])
  },
  beforeMount() {},
  methods: {
    ...mapActions('app', [
      'closeAddToHomeScreenModalForApple',
      'serviceWorkerSkipWaiting'
    ]),

    scrollMeTo(refName) {
      console.log(
        ' ~ file: main.js ~ line 24 ~ scrollMeTo ~ refName',
        refName
      )
      const element = this.$els[refName]
      element.scrollIntoView({ behavior: 'smooth' })
    }
  }
}
</script>

<style lang="scss">
@font-face {
  font-family: 'nathalia_2';
  src: local('nathalia_2'),
    url(./fonts/nathalia_2/Nathalia.ttf) format('truetype');
}

@font-face {
  font-family: 'frozen-berries';
  src: local('frozen-berries'),
    url(./fonts/frozen-berries/frozenberries.ttf) format('truetype');
}

@font-face {
  font-family: 'inter';
  src: local('inter'),
    url('./fonts/inter/Inter-VariableFont_slnt,wght.ttf') format('truetype');
}

body {
  a {
    text-decoration: none;
  }

  #app {
    font-family: 'inter', -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
      Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue,
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 16px;
    color: #2c3e50;

    .new-content-available-toastr {
      position: absolute;
      bottom: 10px;
      right: 10px;
    }

    .apple-add-to-home-screen-modal {
      position: absolute;
      bottom: 0;
      right: 0;
      top: 0;
      left: 0;
      height: fit-content;
      width: fit-content;
      margin: auto;
      z-index: 1000;
    }

    .main-wrapper {
      margin-top: 2rem;
      padding: 20px;

      .page-wrapper {
        width: 95%;
        margin: auto;

        @media screen and (max-width: 1000px) {
          width: 100%;
        }
      }
    }
  }
}
</style>
