export default {
  /* Product input name */
  setDataProductoToCreate: async (state, data) => {
    state.product.id = data.id
    state.product.nombre = data.nombre
    state.product.descripcion = data.descripcion
    state.product.precio = data.precio
    state.product.categoria = data.categoria
    state.product.visible = data.visible
    if (data.id === null) state.product.images = []
  },

  setImageProductoToCreate: async (state, data) => {
    state.product.images.push(data)
  },

  /* Products */
  initIDProduct: (state, id) => (state.product.id = id),
  setProducts: (state, products) => (state.products = products),
  addProduct: (state, product) => state.products.push(product),
  removeProductById: (state, productId) => {
    const index = state.products.findIndex(product => product.id === productId)
    state.products.splice(index, 1)
  },

  /* Products deletion */
  addProductDeletionPending: (state, productId) =>
    state.productDeletionPending.push(productId),
  removeProductDeletionPending: (state, productId) => {
    const index = state.products.findIndex(product => product.id === productId)
    state.productDeletionPending.splice(index, 1)
  },

  /* Product creation */
  setProductCreationPending: (state, value) =>
    (state.productCreationPending = value)
}
