import RootProductsDB from '@/firebase/root-products-db'

export default {
  /**
   * Fetch products of current loggedin user
   */
  getRootProducts: async ({ commit }) => {
    const rootProductDb = new RootProductsDB()

    const products = await rootProductDb.readAll(null, [['order', 'asc']])
    commit('setProducts', products)
  },

  getIDProduct: async ({ commit }) => {
    const rootProductDb = new RootProductsDB()

    const UID = await rootProductDb.getUID()
    commit('initIDProduct', UID)
  },

  setDataProductToEdit: async ({ commit }, product) => {
    commit('setDataProductoToCreate', product)
    product.images.forEach(image => {
      commit('setImageProductoToCreate', image)
    })
  },

  /**
   * Create a product for current loggedin user
   */
  createUserProduct: async ({ commit }, product) => {
    const userProductDb = new RootProductsDB()

    commit('setProductCreationPending', true)
    const createdProduct = await userProductDb.create(product)
    commit('addProduct', createdProduct)
    commit('setProductCreationPending', false)
  },

  updateUserProduct: async ({ commit }, product) => {
    const userProductDb = new RootProductsDB()

    commit('setProductCreationPending', true)
    const createdProduct = await userProductDb.update(product)
    commit('addProduct', createdProduct)
    commit('setProductCreationPending', false)
  },
  /**
   * Create a new product for current loggedin user and reset product name input
   */
  triggerAddProductAction: ({ dispatch, state, commit }) => {
    if (state.product === state.defaultProduct) return

    const product = JSON.parse(JSON.stringify(state.product))
    commit('setDataProductoToCreate', state.defaultProduct)
    dispatch('createUserProduct', product)
  },

  triggerUpdateProductAction: ({ dispatch, state, commit }) => {
    if (state.product === state.defaultProduct) return

    const product = JSON.parse(JSON.stringify(state.product))
    commit('setDataProductoToCreate', state.defaultProduct)
    dispatch('updateUserProduct', product)
  },

  clearDataProduct: ({ state, commit }) => {
    commit('setDataProductoToCreate', state.defaultProduct)
  },

  /**
   * Delete a user product from its id
   */
  deleteUserProduct: async ({ commit, getters }, productId) => {
    if (getters.isProductDeletionPending(productId)) return

    const rootProductsDB = new RootProductsDB()

    commit('addProductDeletionPending', productId)
    await rootProductsDB.delete(productId)
    commit('removeProductById', productId)
    commit('removeProductDeletionPending', productId)
  }
}
