<template>
  <div class="form-contacto-content">
    <validation-observer ref="observer" v-slot="{ invalid }">
      <div class="title-text">
        Contctanos para ms informacin!
      </div>
      <form @submit.prevent="submit">
        <validation-provider
          v-slot="{ errors }"
          name="Nombre"
          rules="required|min:5"
        >
          <v-text-field
            v-model="name"
            :counter="255"
            :error-messages="errors"
            label="Nombre"
            required
          ></v-text-field>
        </validation-provider>

        <validation-provider
          v-slot="{ errors }"
          name="phoneNumber"
          :rules="{
            required: true,
            digits: 10
          }"
        >
          <!--regex: '^(71|72|74|76|81|82|84|85|86|87|88|89)\\d{5}$'-->
          <v-text-field
            v-model="phoneNumber"
            :counter="10"
            :error-messages="errors"
            label="Telfono"
            required
          ></v-text-field>
        </validation-provider>
        <validation-provider
          v-slot="{ errors }"
          name="email"
          rules="required|email"
        >
          <v-text-field
            v-model="email"
            :error-messages="errors"
            label="E-mail"
            required
          ></v-text-field>
        </validation-provider>

        <validation-provider
          v-slot="{ errors }"
          name="comentario"
          rules="required|min:0"
        >
          <v-textarea
            v-model="comentario"
            :counter="255"
            :error-messages="errors"
            label="Comentarios"
            required
          ></v-textarea>
        </validation-provider>

        <v-btn class="mr-4" type="submit" :disabled="invalid">
          Enviar
        </v-btn>
      </form>
    </validation-observer>
    <v-snackbar v-model="snackbar" color="success">
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn text v-bind="attrs" @click="snackbar = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
    <v-snackbar v-model="snackbarError" color="error">
      {{ textError }}

      <template v-slot:action="{ attrs }">
        <v-btn text v-bind="attrs" @click="snackbarError = false">
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>
<script>
import apiMail from '@/api/mail'
import { required, digits, email, max, regex } from 'vee-validate/dist/rules'
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode
} from 'vee-validate'

setInteractionMode('eager')

extend('digits', {
  ...digits,
  message: '{_field_} needs to be {length} digits. ({_value_})'
})

extend('required', {
  ...required,
  message: '{_field_} can not be empty'
})

extend('max', {
  ...max,
  message: '{_field_} may not be greater than {length} characters'
})

extend('regex', {
  ...regex,
  message: '{_field_} {_value_} does not match {regex}'
})

extend('email', {
  ...email,
  message: 'Email must be valid'
})

export default {
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data: () => ({
    name: '',
    phoneNumber: '',
    email: '',
    comentario: '',
    snackbar: false,
    snackbarError: false,
    text: 'Recibimos tu informacin, en unos momentos obtendrs respuesta',
    textError: 'Error al solicitar informacin, por favor intentelo de nuevo'
  }),

  methods: {
    async submit() {
      const data = await this.$refs.observer.validate()
      if (data) {
        const obj = {
          nombre: this.name,
          telefono: this.phoneNumber,
          correo: this.email,
          comentarios: this.comentario
        }
        const res = await apiMail.sendMail(obj)
        if (res.data) {
          console.log(
            ' ~ file: FormContacto.vue ~ line 145 ~ submit ~ res.data',
            res.data
          )
          if (res.data.success) {
            console.log(
              ' ~ file: FormContacto.vue ~ line 147 ~ submit ~ res.data.success',
              res.data.success
            )
            this.snackbar = true
            this.clear()
          } else {
            this.snackbarError = true
          }
        }
      }
    },
    clear() {
      this.name = ''
      this.phoneNumber = ''
      this.email = ''
      this.comentario = ''
      this.$refs.observer.reset()
    }
  }
}
</script>
<style lang="scss" scoped>
@import '@/theme/variables.scss';

.form-contacto-content {
  background-color: white;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 500px;
}

.content-input {
  padding-left: 10px;
  padding-right: 10px;
  // background-color: gray;
}

.title-text {
  font-size: 20px;
  font-weight: 900;
}

.subtitle-text {
  font-size: 15px;
  font-weight: 500;
}
</style>
