<template>
  <div :style="orderElementsStyle" class="container-card">
    <div>
      <img :src="require(`@/assets/${url}`)" :alt="url" class="image" />
    </div>
    <div :style="orderTextStyle" class="container-information">
      <slot name="title-text"> </slot>
      <slot name="body-text"> </slot>
      <slot name="small-text"> </slot>
      <slot name="actions-section">
        <!-- <div class="buttons-container">
        <div class="button-more"><p>Ms informacin</p></div>
        <div class="button-petition">Lo quiero!</div>
      </div> -->
      </slot>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    url: {
      type: String,
      required: true
    },
    index: Number
  },
  computed: {
    orderElementsStyle() {
      if (this.index % 2) {
        return {
          '--order-element': 'row-reverse'
        }
      }
      return {
        '--order-element': 'row'
      }
    },
    orderTextStyle() {
      if (this.index % 2) {
        return {
          '--align-items': 'flex-start',
          '--order-text': 'left'
        }
      }
      return {
        '--align-items': 'flex-end',
        '--order-text': 'right'
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.container-card {
  //   overflow: auto;
  // border-radius: 1px;

  width: 100%;
  // margin-bottom: 30px;
  padding: 30px;

  // Flex
  display: flex;
  flex-direction: var(--order-element);
  justify-content: space-evenly;
  align-items: center;
  padding-right: 100px;
}

.image {
  display: block;
  max-width: 500px;
  height: auto;
  // flex-shrink: 1;
}

.container-information {
  //   color: whitesmoke;
  display: flex;
  flex-direction: column;
  align-items: var(--align-items);
  max-width: 700px;
  text-align: var(--order-text);
}

.buttons-container {
  display: flex;
}

.button-more {
  color: white;
  background-color: rgb(26, 115, 232);
  width: 200px;
  height: 40px;
  text-align: center;

  border-radius: 2px;

  line-height: 40px;
  font-weight: 600;
  font-size: 17px;

  margin-bottom: -10px;
  cursor: pointer;
}

.button-more:hover {
  background-color: rgb(90, 135, 255);
}

@media (max-width: 960px) {
}

@media (max-width: 800px) {
  .container-information {
    text-align: center;
  }

  .buttons-container {
    justify-content: center;
  }
}
</style>
